<template>
  <div class="no-bottom no-top" id="content">
    <div id="top"></div>

    <!-- section begin -->
    <section
      id="subheader"
      class="text-light"
      :style="styleObject"
      data-bgimage="url(/images/background/8.jpg) top"
    >
      <div class="center-y relative text-center">
        <div class="container">
          <div class="row">
            <div class="col-md-12 text-center">
              <h1>{{ info.name }}</h1>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
    </section>
    <!-- section close -->

    <!-- section begin -->
    <section aria-label="section">
      <div class="container">
        <div class="wow fadeIn w-100">
          <div>
            <div class="d-flex justify-content-between w-100">
              <div class="col-6">
                <div
                  v-for="layer in layers"
                  :key="layer.id"
                  class="col-12 mb-4"
                >
                  <div class="d-project d-flex col-12">
                    <!-- <img :src="'./images/ui/plus.svg'" alt="" deleteLayer(layer.id)/> -->
                    <div class="d-flex flex-column arrows_drag pointer">
                      <span
                        v-if="layer.number !== layers[0].number"
                        @click="
                          replaceLayerUp(
                            layer.id,
                            layers[layers.indexOf(layer) - 1].id,
                            layer.number,
                            layers[layers.indexOf(layer) - 1].number
                          )
                        "
                        aria-hidden="true"
                        class="arrow_triangle-up_alt"
                      ></span>
                      <span v-else style="height: 16px"></span>
                      <span
                        v-if="layer.number !== layers[layers.length - 1].number"
                        @click="
                          replaceLayerDown(
                            layer.id,
                            layers[layers.indexOf(layer) + 1].id,
                            layer.number,
                            layers[layers.indexOf(layer) + 1].number
                          )
                        "
                        aria-hidden="true"
                        class="arrow_triangle-down_alt"
                      ></span>
                      <span v-else style="height: 16px"></span>
                    </div>

                    <p
                      class="layer_name pointer"
                      @click="openLayer(layer.id)"
                      style="margin-right: 16px"
                    >
                      {{ layer.name }}
                       {{ layer.number }}
                    </p>
                    <div
                      v-for="image in layerImages[layer.id]"
                      class="
                        d-flex
                        flex-column
                        align-items-end
                        position-relative
                        me-2
                      "
                      :key="image.id"
                    >
                      <div
                        class="delete_layer deletex pointer"
                        @click="toggleModalForPic(image.id, layer.id)"
                      ></div>
                      <div v-if="showModalPic">
                        <Modal
                          :message="message"
                          @close="toggleModalForPic"
                          @delete="deletePicture(deletePicId, deletePicLayerId)"
                        />
                      </div>
                      <img
                        :id="image.id"
                        src="https://www.pulsecarshalton.co.uk/wp-content/uploads/2016/08/jk-placeholder-image.jpg"
                        style="width: 40px; margin-right: 8px"
                        alt=""
                      />
                    </div>
                    <div
                      @click="toggleModalForLayer(layer.id)"
                      class="delete_btn deletex pointer"
                    ></div>
                  </div>
                  <div v-if="showModalLayer">
                    <Modal
                      :message="message"
                      @close="toggleModalForLayer"
                      @delete="deleteLayer(modalData)"
                    />
                  </div>
                </div>

                <form
                  id="form-create-item"
                  class="form-border"
                  v-on:keydown.enter.prevent="createProject"
                >
                  <div class="field-set">
                    <div class="de_tab tab_methods">
                      <div class="de_tab_content">
                        <div id="tab_opt_1">
                          <h5>Layer Name</h5>
                          <input
                            type="text"
                            name="layer_name"
                            id="layer_name"
                            v-model="layerName"
                            class="form-control"
                            placeholder="enter layer name"
                          />
                        </div>
                      </div>
                    </div>

                    <div class="spacer-10"></div>

                    <input
                      type="button"
                      id="submit"
                      class="btn-main"
                      value="Create Item"
                      @click.prevent="createLayer"
                      v-on:keydown.enter.prevent="createProject"
                    />
                  </div>
                </form>
              </div>
              <div class="col-5 d-flex flex-column">
                <input
                  list="layers"
                  v-model="selected"
                  name="myBrowser"
                  @change="selectedLayerGet()"
                  placeholder="Start typing layers name"
                  class="select__layer mb-4"
                />
                <datalist id="layers">
                  <option id="layers" v-for="layer in layers" :key="layer.id">
                    {{ layer.name }}
                  </option>
                </datalist>
                <div>
                  <UploadImages
                    @changed="handleImages"
                    :max="1"
                    id="profile-pic"
                    class="d-create-file"
                  />
                  <input
                    type="button"
                    id="submit"
                    class="btn-main mt-3"
                    value="Upload Image"
                    @click="addPicToLayout"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import axios from "axios";
import Modal from "../components/Modal.vue";
import UploadImages from "vue-upload-drop-images";
import VueSelect from "vue-next-select";
import "vue-next-select/dist/index.css";
import { ref } from "vue";
import filterSelect from "../components/filterSelect.vue";

export default {
  components: { Modal, UploadImages, VueSelect, filterSelect },
  data() {
    return {
      info: "",
      image: null,
      layerName: "",
      number: null,
      showModalPic: false,
      showModalLayer: false,
      modalData: "",
      selected: "",
      currentLayerId: null,
      currentLayerIdNeigh: null,
      currentLayerNumber: null,
      currentLayerNumberNeigh: null,
      upCurrentLayerId: null,
      upCurrentLayerIdNeigh: null,
      upCurrentLayerNumber: null,
      upCurrentLayerNumberNeigh: null,
      selectedLayer: "",
      deletePicLayerId: "",
      deletePicId: "",
      message: "",
      layers: [],
      country: "",
      layerImages: {},
      styleObject: {
        background: "url(/images/background/8.jpg)",
      },
    };
  },
  mounted() {
    this.$store.commit("darkHeader");
    axios
      .get("https://api.nftslab.io/api/v1/projects/" + this.id)
      .then((response) => (this.info = response.data.project));
    this.getLayers();
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
  },
  methods: {
    toggleModalForLayer(data) {
      this.message = "Are you sure you want to delete this Layer? ";
      this.modalData = data;
      this.showModalLayer = !this.showModalLayer;
    },

    toggleModalForPic(imgId, layerId) {
      this.message = "Are you sure you want to delete this picture? ";
      this.deletePicLayerId = layerId;
      this.deletePicId = imgId;
      // console.log(this.deletePicLayerId);
      this.showModalPic = !this.showModalPic;
    },

    createLayer() {
      let layerNumber = this.layers[this.layers.length-1]?.number + 1;
      if (this.layerName) {
        axios
          .put(
            "https://api.nftslab.io/api/v1/projects/" + this.id + "/layers",
            {
              Name: this.layerName,
              Number: layerNumber ? layerNumber : 1
            }
          )
          .then((response) => {
            this.getLayers();
          })
          .catch((response) => {
            console.log(response);
          });
      }
    },
    openLayer(id) {
      console.log(id);
      console.log(this.id);
      this.$router.push({
        name: "MyLayer",
        params: { projectId: this.id, id: id },
      });
    },
    getLayers() {
      axios
        .post("https://api.nftslab.io/api/v1/projects/" + this.id + "/layers", {
          From: 0,
          To: 10000,
        })
        .then((response) => {
          this.layers = response.data.layers;
          this.layers.forEach((element) => {
            this.getImages(element.id);
          });
        })
        .catch((error) => {
          console.log(error);
        });
    },
    deleteLayer(id) {
      axios
        .delete(
          "https://api.nftslab.io/api/v1/projects/" +
            this.id +
            "/layers" +
            "/" +
            id
        )
        .then((response) => {
          // console.log(response);
          this.$store.commit("alert", {
            text: response.data.message,
            type: "success",
          });
          this.getLayers();
          this.showModalLayer = false;
        });
    },

    deletePicture(picId, LayerId) {
      // let picId = this.deletePicId
      axios
        .delete(
          "https://api.nftslab.io/api/v1/projects/" +
            this.id +
            "/layers" +
            "/" +
            LayerId +
            "/images" +
            "/" +
            picId
        )
        .then((response) => {
          // console.log(response);
          this.$store.commit("alert", {
            text: response.data.message,
            type: "success",
          });
          this.getLayers();
          this.showModalPic = false;
        });
    },

    handleImages(files) {
      if (files[0]) {
        if (
          files[0].size < 2097152 &&
          (files[0].type !== "image/png" ||
            files[0].type !== "image/jpeg" ||
            files[0].type !== "image/gif")
        ) {
          this.image = files[0];
          // this.errorPic = ""
        } else {
          this.$store.commit("alert", {
            text: "Image must be PNG, JPEG or GIF and max 2MB",
            type: "danger",
          });
          // this.errorPic = "Image must be PNG, JPEG or GIF and max 2MB"
          document.querySelector(".imgsPreview .clearButton").click();
        }
      }
    },
    addPicToLayout() {
      var formData = new FormData();
      formData.append("image", this.image);
      axios
        .put(
          "https://api.nftslab.io/api/v1/projects/" +
            this.id +
            "/layers/" +
            this.selectedLayer +
            "/images",
          formData,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((response) => {
          this.getLayers();
        })
        .catch((response) => {
          console.log(response);
        });
    },
    getImages(id) {
      axios
        .post(
          "https://api.nftslab.io/api/v1/projects/" +
            this.id +
            "/layers/" +
            id +
            "/images",
          {}
        )
        .then((response) => {
          this.layerImages[id] = response.data.images;
          console.log(this.layerImages);
          this.layerImages[id].forEach((element) => {
            let imageId = element.id;
            let imgUrl =
              "https://api.nftslab.io/api/v1/projects/" +
              this.id +
              "/layers/" +
              id +
              "/images/static/" +
              imageId +
              ".png";
            let Token = this.$store.state.auth.token;
            this.displayProtectedImage(imageId, imgUrl, Token);
          });
        })
        .catch((response) => {
          console.log(response);
        });
    },

    selectedLayerGet() {
      this.selectedLayer = this.layers.find((el) => {
        return el.name === this.selected;
      })?.id;
    },
    replaceLayerDown(currId, nextId, currNumb, nextNumb) {
      this.currentLayerId = currId;
      this.currentLayerIdNeigh = nextId;
      this.currentLayerNumber = currNumb;
      this.currentLayerNumberNeigh = nextNumb;
      axios
        .patch(
          "https://api.nftslab.io/api/v1/projects/" +
            this.id +
            "/layers/" +
            this.currentLayerId,
          {
            Number: this.currentLayerNumberNeigh,
          }
        )
        .then((response) => {}),
        axios
          .patch(
            "https://api.nftslab.io/api/v1/projects/" +
              this.id +
              "/layers/" +
              this.currentLayerIdNeigh,
            {
              Number: this.currentLayerNumber,
            }
          )
          .then((response) => {
            this.getLayers();
          });
    },
    replaceLayerUp(currId, prevId, currNumb, prevNumb) {
      this.upCurrentLayerId = currId;
      this.upCurrentLayerIdNeigh = prevId;
      this.upCurrentLayerNumber = currNumb;
      this.upCurrentLayerNumberNeigh = prevNumb;
      axios
        .patch(
          "https://api.nftslab.io/api/v1/projects/" +
            this.id +
            "/layers/" +
            this.upCurrentLayerId,
          {
            Number: this.upCurrentLayerNumberNeigh,
          }
        )
        .then((response) => {}),
        axios
          .patch(
            "https://api.nftslab.io/api/v1/projects/" +
              this.id +
              "/layers/" +
              this.upCurrentLayerIdNeigh,
            {
              Number: this.upCurrentLayerNumber,
            }
          )
          .then((response) => {
            this.getLayers();
          });
    },
    async displayProtectedImage(imageId, imageUrl, authToken) {
      // Fetch the image.
      const response = await this.fetchWithAuthentication(imageUrl, authToken);

      // Create an object URL from the data.
      const blob = await response.blob();
      const objectUrl = URL.createObjectURL(blob);

      // Update the source of the image.
      const imageElement = document.getElementById(imageId);
      // console.log(imageId , "ID");
      imageElement.src = objectUrl;
      imageElement.onload = () => {
        URL.revokeObjectURL(objectUrl);
      };
    },
    fetchWithAuthentication(url, authToken) {
      const headers = new Headers();
      headers.set("Authorization", `Bearer ${authToken}`);
      return fetch(url, { headers });
    },
  },
};
</script>

<style scoped>
.position {
  bottom: -20px;
  right: 5px;
  position: absolute;
  font-size: 12px;
  font-style: italic;
  opacity: 80%;
}
.plus-icon--layers {
  padding: 30px;
}
.delete_btn {
  right: 8px;
  top: 50%;
  transform: translateY(-50%);
}
.layer_name {
  left: 50px !important;
}
.layer_name {
  margin-bottom: 0;
}
.select__layer {
  padding: 15px;
  border-radius: 10px;
  border: solid 3px#dddddd;
}
select {
  padding: 15px;
  border-radius: 10px;
  border: solid 3px #dddddd;
}
.d-project {
  text-align: unset;
  align-items: center;
}
.arrows_drag {
  margin-right: 8px;
  height: 40px;
  justify-content: space-between;
}
.delete_layer {
  position: absolute;
  right: 0;
  top: -5px;
}
.delete_layer:after {
  color: rgb(150, 148, 148) !important;
}
</style>