<template>
    <div class="backdrop" @click.self="closeModal">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
                        <button type="button" @click.self="closeModal" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        {{message}}
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-secondary" @click.self="closeModal" data-bs-dismiss="modal">Close</button>
                        <button type="button" class="btn btn-primary" @click.self="deleteElement">Delete</button>
                    </div>
                </div>
            </div>
    </div>     
</template>

<script>
export default {
    props: ['message'],
    methods: {
        closeModal(){
            this.$emit('close')
        },
        deleteElement(){
            this.$emit('delete')
        }
    }
}
</script>

<style>
    .backdrop {
        top: 0;
        left: 0;
        position: fixed;
        background: rgba(0, 0, 0, 0.5);
        width: 100%;
        height: 100%;
        z-index: 100;
    }
    .modal-dialog {
        margin: 100px auto;
    }
</style>