<template>
  <templateHeader  />

  <router-view/>

  <templateFooter />
</template>

<script>
import templateHeader from '@/components/header.vue'
import templateFooter from '@/components/footer.vue'
import axios from 'axios'

export default {
  components: {
    templateHeader,
    templateFooter,
  },
  data() {
      return {
          token: null
      }
  },
  mounted() {
    if(localStorage.user){
      this.$store.commit("auth/updateUser", JSON.parse(localStorage.user));
      this.$store.commit('auth/updateProfilePic');
      if(JSON.parse(localStorage.user).token) {
        let token = this.parseJwt(JSON.parse(localStorage.user).token);
        console.log(token.exp);
        let now = Date.now().toString();
        now = now.substr(0, 10);
        if(parseInt(now) > token.exp) {
          this.$store.commit('auth/logout');
          console.log('Token expired');
        } else {
          console.log("Token is valid");
        }
      }
      
    }
  },
  beforeUnmount() {
      localStorage.user = JSON.stringify(this.$store.state.auth)
  },
  methods : {
    parseJwt(token) {
      var base64Url = token.split('.')[1];
      var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
      var jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
          return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
      }).join(''));

      return JSON.parse(jsonPayload);
    }
  },

}

</script>

<style lang="scss">

#nav {
  padding: 30px;

  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #42b983;
    }
  }
}
</style>