<template>
  <div class="no-bottom no-top" id="content">
    <div id="top"></div>

    <!-- section begin -->
    <section
      id="subheader"
      class="text-light"
      data-bgimage="url(/images/background/subheader.jpg) top"
    >
      <div class="center-y relative text-center">
        <div class="container">
          <div class="row">
            <div class="col-md-12 text-center">
              <h1>My Projects</h1>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
    </section>
    <!-- section close -->

    <!-- section begin -->
    <section aria-label="section">
      <div class="container">
        <div class="row wow fadeIn">
          <div class="col-lg-8 col-md-12 offset-lg-1">
            <div class="row">

              <div class="col-md-3 col-6 mt-2">
                <div class="d-create-file plus-icon pointer" @click="addProject">
                  <img src="/images/ui/plus.svg" alt="" />
                  Add Project
                </div>
              </div>

              <div v-for="project in projects" :key="project.id" class="col-md-3 col-6 mt-2">
                <div class="d-project plus-icon">
                  <!-- <img :src="'./images/ui/plus.svg'" alt="" @click="deleteProject(project.id)"  /> -->
                  <p class="project_name pointer" @click="openProject(project.id)">{{project.name}}</p>
                  <div @click="toggleModal(project.id)" class="deletex pointer"></div>
                </div>
                <div v-if="showModal"> 
                 <Modal :message='message' @close="toggleModal()" @delete="deleteProject(modalData)"/>
                </div> 
              </div>

            </div>
            
            <form
              id="form-create-item"
              class="form-border"
              v-on:keydown.enter.prevent='createProject'
            >
              <div class="field-set" v-if="show">
                <div class="de_tab tab_methods">
                  <div class="de_tab_content">
                    <div id="tab_opt_1">
                      <h5>Project Name</h5>
                      <input
                        type="text"
                        name="project_name"
                        id="project_name"
                        v-model="projectName"
                        class="form-control"
                        placeholder="enter project name"
                      />
                    </div>
                  </div>
                </div>

                <div class="spacer-10"></div>

                <input
                  type="button"
                  id="submit"
                  class="btn-main"
                  value="Create Item"
                  @click.prevent="createProject"
                  v-on:keydown.enter.prevent='createProject'
                />

                
              </div>
            </form>
          </div>
        </div>
      </div>
    </section>
  </div>

</template>

<script>
import axios from "axios";
import Modal from "../components/Modal.vue"
export default {
  components: {Modal},
  data() {
      return {
          show: false,
          projectName: "",
          modalData: "",
          projects: [],
          showModal: false,
          message: "Are you sure you want to delete this project? "
      }
  },
  mounted() {
    this.$store.commit("darkHeader");
    
    this.getProjects();
  },
  unmounted() {
    this.$store.commit("lightHeader");
  },
  methods: {
      toggleModal(data) {
        this.modalData = data;
        this.showModal=!this.showModal
      },
      addProject() {
          this.show = !this.show;
          console.log(this.projects);
      },
      createProject() {
          if (this.projectName){
              axios.put('https://api.nftslab.io/api/v1/projects', {
                name:this.projectName
              })
              .then((response) => {
                this.getProjects();
              })
              .catch((response) => {
                  console.log(response);
              })
          }
      },
      getProjects() {
        axios.post('https://api.nftslab.io/api/v1/projects', {
          From:0,
          To: 10
        })
        .then((response) => {
            this.projects = response.data.projects;
        })
        .catch((error) => {
            console.log(error);
        })
      },
      openProject(id) {
        this.$router.push({ name: 'MyProject', params: { id: id } })
      },
      deleteProject(id) {
          axios.delete('https://api.nftslab.io/api/v1/projects/'+ id)
          .then((response) => {
            // console.log(response);
            this.$store.commit('alert', {text: response.data.message, type : "success"});
            this.getProjects();
            this.showModal=false;
          })
      }
  }
};
</script>

<style>
.plus-icon {
  width: 180px;
  height: 180px;
}
.plus-icon img {
  width: 100%;
  height: 100%;
}
.project_name {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-wrap: break-word;
  font-weight: bold;
}
.deletex:after {
  display: inline-block;
  content: "\00d7";
  font-size: 25px;
  border-radius: 50%;
  color: #8364E2;
  font-weight: bold;
  z-index: 100;
}
.deletex {
  position: absolute;
  right: 8px;
  top: 8px;
  line-height: 0.5;
}
</style>