<template>
  <div class="no-bottom no-top" id="content">
    <div id="top"></div>
    <section
      id="subheader"
      class="text-light"
      data-bgimage="url(/images/background/6.jpg) top"
    >
      <div class="center-y relative text-center">
        <div class="container">
          <div class="row">
            <div class="col-md-12 text-center">
              <h1>{{ info.name }}</h1>
              <h3>Layer #{{ info.number }}</h3>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>
      </div>
    </section>
    <div class="container">
      <div class="row my-4">
        <div v-for="image in layerImages" :key="image.id"
            class="col-lg-2 col-md-3 col-4">
          <img
            :id="image.id"
            src="https://www.pulsecarshalton.co.uk/wp-content/uploads/2016/08/jk-placeholder-image.jpg"
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      info: "",
      layerImages: {},
    };
  },
  computed: {
    id() {
      return this.$route.params.id;
    },
    projectId() {
      return this.$route.params.projectId;
    },
  },
  mounted() {
    this.$store.commit("darkHeader");
    axios
      .get(
        "https://api.nftslab.io/api/v1/projects/" +
          this.projectId +
          "/layers/" +
          this.id
      )
      .then((response) => (this.info = response.data.layer));
    axios
      .post(
        "https://api.nftslab.io/api/v1/projects/" +
          this.projectId +
          "/layers/" +
          this.id +
          "/images",
        {}
      )
      .then((response) => {
        this.layerImages = response.data.images;
        this.dispalyImages();

        //this.getLayers();
      })
      .catch((response) => {
        console.log(response);
      });
  },
  methods: {
    async displayProtectedImage(imageId, imageUrl, authToken) {
      // Fetch the image.
      const response = await this.fetchWithAuthentication(imageUrl, authToken);

      // Create an object URL from the data.
      const blob = await response.blob();
      const objectUrl = URL.createObjectURL(blob);

      // Update the source of the image.
      const imageElement = document.getElementById(imageId);
      // console.log(imageId , "ID");
      imageElement.src = objectUrl;
      imageElement.onload = () => {
        URL.revokeObjectURL(objectUrl);
      };
    },
    fetchWithAuthentication(url, authToken) {
      const headers = new Headers();
      headers.set("Authorization", `Bearer ${authToken}`);
      return fetch(url, { headers });
    },
    dispalyImages() {
      this.layerImages.forEach((element) => {
        let imageId = element.id;
        let imgUrl =
          "https://api.nftslab.io/api/v1/projects/" +
          this.projectId +
          "/layers/" +
          this.id +
          "/images/static/" +
          imageId +
          ".png";
        let Token = this.$store.state.auth.token;
        this.displayProtectedImage(imageId, imgUrl, Token);
      });
    },
  },
};
</script>

